// Bot√≥n subir pagina.
// Usa FontAwesome para la fuente.
.to-top {
  background: rgba(0, 0, 0, .3);
  border-radius: 30px;
  bottom: 10px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, .5);
  display: none;
  height: 46px;
  position: fixed;
  right: 10px;
  width: 46px;

  &:before {
    color: rgb(68, 110, 155);
    content: "\f106";
    font-family: "FontAwesome";
    font-size: 36px;
    left: 12px;
    position: relative;
    top: -5px;
  }

  &:hover {
    background: #4A148C;
    cursor: pointer;
  }

  &:hover:before {
    color: rgb(255, 255, 255);
  }
}
